import Vue from 'vue'
import Router from 'vue-router'
import ItemView from './views/item.vue'
import ListView from './views/list.vue'
// import Home from './views/home.vue'
// import EventEdit from './views/event-form.vue'

// console.log('working')
Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'list',
            component: ListView
        },
        {
            path: '/item1',
            name: 'item1',
            props: true,
            component: ItemView
        }
        // {
        //     path: '/item2',
        //     name: 'item2',
        //     props: true,
        //     component: ItemView
        // },
        // {
        //     path: '/item3',
        //     name: 'item3',
        //     props: true,
        //     component: ItemView
        // }
    ]
})




